<template>
  <div class='navP'>
    <div class='container'>
    <ul>
      <router-link :to="{ name: 'Notes', params: { act: 'Notes' } }">
      <li class="nav-item">
          <a class="navbar-brand" href="#">Kiwido</a>
      </li>
    </router-link>
      <router-link :to="{ name: 'Notes', params: { act: 'Notes' } }">
        <li class="nav-item over" :class="{ active: act == 'Notes' }" @click="act = 'Notes'">
          <a class="nav-link">Notes</a>
        </li>
      </router-link>
      <router-link :to="{ name: 'Boards', params: { act: 'Notes' } }">
        <li class="nav-item over" :class="{ active: act == 'Boards' }" @click="act = 'Boards'">
          <a class="nav-link">Boards</a>
        </li>
      </router-link>
      <li class="nav-item" style="float:right" v-if='username'>
        <form class="form-inline my-2 my-lg-0">
          <button class="btn btn-outline-success my-2 my-sm-0" type="submit" @click='logout'>Log out</button>
        </form>
      </li>
    </ul>
  </div>
</div>
</template>

<script>
export default {
  name: 'Auth',
  data(){
    return {
      username: '',
      act: 'Notes'
    }
  },
  props: ['act'],
  created(){
    this.axios.get('//kiwido.site/auth/me/',{
      headers: {
        Authorization: 'Token ' + localStorage.token
      }
    }).then((response) => {
      this.username = response.data.username
    })
  },
  methods:{
    logout(){
      localStorage.token = ''
      document.location.reload(true)
    }
  }
}
</script>

<style scoped>
.over:hover {
  color: grey!important;
}
a {
    text-decoration: none!important;
    color: black;
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: white;
}

li {
  float: left;
}

li a {
  display: block;
  color:black;
  text-align: center;
  text-decoration: none;
}

/* Change the link color to #111 (black) on hover */
li a:hover {
  background-color: ;
}

navP{
  z-index: 01;
  width:100%;
  top: 0;
  margin-bottom: 20px;
}
