<template>
  <div class='DeskP'>
    <div class='container mar' v-if='!show'>
      <div class='mar2'><h1>Your Desks<button @click='add=true'>+</button></h1></div>
      <draggable
        class="dragArea list-group "
        :list="desks"
        :clone="clone"
        :group="{ name: 'people', pull: pullFunction }"
        @start="start"
        @end='onEnd'
      >
      <div class='row' v-for='desk in desks' :key="desk.id">
        <div class='col-4'>
          <router-link :to="{ name: 'Board', params: { id: desk.id } }">
            <div class='desk todo-item'  @click='show=desk.name' v-bind:style="{ background: '#'+desk.color}">
              <h4>{{desk.name}}</h4>
              <div>
                <div class='remove-item' @click='deleteDesk(desk)'>
                  <h1>&times;</h1>
                </div>
              </div>
            </div>
          </router-link>
        </div>
      </div>
    </draggable>
    </div>

    <div v-if='trashShow' class='container trash'>
      <h4>TRASH</h4>
      <draggable
        class="dragArea list-group"
        :list="trash"
        :clone="clone"
        :group="{ name: 'people', pull: pullFunction }"
        @start="start"
        @end='onEndTrash'
      >
        <div class='row' v-for='desk in trash' :key="desk.id">
          <div class='col-4'>
            <div class='desk' @click='show=desk.name' v-bind:style="{ background: '#'+desk.color}">
              <h4 class='trashText'>move here to delete</h4>
            </div>
          </div>
        </div>
      </draggable>

    </div>

    <div class='newDesk container' v-if='add' @blur='add=false'>
      <div><h1 class='cansel' @click='add=false'>&times;</h1></div>
      <h1>Create new desk</h1>
      <input placeholder='Name of new desk' v-model='newDeskName'>
      <h4>Color:</h4>
      <div class='line'>
        <div class='color' :class="{ active: colorF == '76e57f' }" @click="colorF = '76e57f'"  style="background-color: #76e57f; "></div>
        <div class='color' :class="{ active: colorF == 'e576d2' }" @click="colorF = 'e576d2'" style="background-color: #e576d2; "></div>
        <div class='color' :class="{ active: colorF == 'e5ae76' }" @click="colorF = 'e5ae76'" style="background-color: #e5ae76; "></div>
        <div class='color' :class="{ active: colorF == '7679e5' }" @click="colorF = '7679e5'" style="background-color: #7679e5; "></div>
      </div>
      <div class='line'>
        <div class='color' :class="{ active: colorF == 'e57676' }" @click="colorF = 'e57676'"  style="background-color: #e57676; "></div>
        <div class='color' :class="{ active: colorF == 'e5e276' }" @click="colorF = 'e5e276'" style="background-color: #e5e276; "></div>
        <div class='color' :class="{ active: colorF == '76e0e5' }" @click="colorF = '76e0e5'" style="background-color: #76e0e5; "></div>
        <div class='color' :class="{ active: colorF == '828189' }" @click="colorF = '828189'" style="background-color: #828189; "></div>
      </div>
      <button class='buttonLogin' @click='postDesk'>Create</button>
    </div>


  </div>
</template>
<script>
import draggable from 'vuedraggable'

export default{
  components: {
    draggable
  },
  data(){
    return {
      trashShow: '',
      colorF: '',
      trash: [
        {name:'перетащите сюда', color: 'e57676', id: 1}
      ],
      add: false,
      newDeskName: '',
      link: '',
      desks: null,
      show: '',
      controlOnStart: true,
    }
  },
  created(){
    this.link = '//kiwido.site/api/desk/get/'
    this.link = this.link + localStorage.token +'/'
    this.axios.get(
      this.link
    ).then((response) => {
      this.desks = response.data
    })
  },
  methods:{
    postDesk(){
      this.link = '//kiwido.site/api/desk/post/'
      this.link = this.link + localStorage.token +'/'+this.newDeskName + '/' + this.colorF + '/' + 0 +'/'
      this.axios.get(
        this.link
      ).then((response) => {
        this.add = false
        document.location.reload(true)
      })
      document.location.reload(true)
    },
    deleteDesk(desk){
      this.link = '//kiwido.site/api/desk/delete/'
      this.link = this.link + localStorage.token +'/' + desk.id +'/'
      this.axios.get(
        this.link
      ).then((response) => {
        document.location.reload(true)
      })
      document.location.reload(true)
    },
    editDesk(desk){
      alert('sad')
    },
    onEndTrash(){
    },
    clone({ name, id }) {
      return { name, id };
    },
    pullFunction() {
      return this.controlOnStart ? "clone" : true;
    },
    start({ originalEvent }) {
      this.controlOnStart = originalEvent.ctrlKey;
      this.trashShow = true
    },
    onEnd(){
      this.trashShow = false
      for (var i in this.desks){
        this.link = '//kiwido.site/api/desk/edit/'
        this.link = this.link + localStorage.token +'/'+this.desks[i].name + '/' + this.desks[i].color + '/' + i + '/' + this.desks[i].id + '/'
        this.axios.get(
          this.link
        ).then((response) => {
          this.add = false
        })
      }
      for (var i in this.trash){
        if (this.trash[i]['name'] != 'перетащите сюда'){
          this.link = '//kiwido.site/api/desk/delete/'
          this.link = this.link + localStorage.token +'/' + this.trash[i]['id'] +'/'
          this.axios.get(
            this.link
          ).then((response) => {
          })
        }
      }
      this.trash = [{name:'перетащите сюда', color: 'e57676', id: 1}]
    }
  }
}
</script>

<style >
.DeskP a {
  text-decoration: none;
}
 .trashText{
  text-align: center!important;
}
 .trash{
  position: fixed;
  top: 90%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  box-shadow: 0 0 10px rgba(0,0,0,0.3);
  padding-top: 10px;
  padding: 50px;
  color: green;
  text-align: center;
}
.DeskP .imgEdit{
  padding-right: 20px;
  padding-left: 20px;
}
.DeskP .cansel{
  cursor:pointer;
  text-align: right;
}
.DeskP .active{
  width: 55px!important;
  height: 55px!important;
}
.DeskP .buttonLogin {
  border-radius: 20px;
  background-color: white;
  border: 3px solid lightgreen;
  height: 50px;
  width: 150px;
  font-size: 20px;
  color: #7e838a;
  cursor: pointer;
  transition: 0.3s;
  margin-top: 20px;
}
.DeskP .buttonLogin:hover {
  background-color: lightgreen;
  color: white;
}
.DeskP .color{
  border-radius: 100px;
  height:40px;
  width: 40px;
  cursor: pointer;
  text-align:center;
  margin: 10px;
}
.DeskP .line{
  display: -webkit-box;
  justify-content: center;
}
.DeskP input{
  outline: none;
  border: 3px solid lightblue;
  background: none;
  border-radius: 50px;
  margin:20px auto;
  width:100%;
  padding: 15px;
  text-align: center;
  font-size: 20px;
  transition: 0.3s;
}
.DeskP .newDesk{
  text-align: center;
  width: 100%;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  box-shadow: 0 0 10px rgba(0,0,0,0.3);
  padding: 50px;
}
.DeskP button{
  border: 1px solid #7679e5;
  color: #7679e5;
  background: white;
  border-radius: 5px;
  margin-left: 10px;
  width: 50px;
}

.DeskP .mar2{
  margin-bottom: 3px;
  margin-top: 100px;
}
.DeskP .desk{
  background-color: #7679e5;
  color:white;
  padding:40px;
  border-radius: 20px;
  margin-top:10px;
  width:100%;
  cursor: pointer;
  display: flex;
  align-items: center;
}
.DeskP .col-4{
  min-width: 100%;
  width:100%;
}
body{
  background: white!important;
}
</style>
