<template>
<div class='auth'>
  <div v-on:keyup.enter="login" class='boxLogin' v-if='!showReg'>
    <h1>Login</h1>
    <p class='error' v-if='errorLogin'>{{errorLogin}}</p>
    <div>
      <input type='login' class='inputLogin' placeholder='login' v-model='username'>
    </div>
    <div>
      <input type='password' class='inputLogin' placeholder='password' v-model='password'>
    </div>
    <button class='buttonLogin' v-on:keyup.enter="login" @click='login'>Login</button>
    <p @click='show' class='linkRegister'>Sing up?</p>
  </div>
  <div v-on:keyup.enter="singup" class='boxLogin' v-if='showReg'>
    <h1>Register</h1>
    <p class='error' v-if='errorPass'>{{errorPass}}</p>
    <div v-for='i in er'><p class='error'>{{i}}</p></div>
    <div>
      <input type='email' class='inputLogin' placeholder='Email' v-model='email' required>
    </div>
    <div>
      <input type='text' class='inputLogin' size=150 placeholder='username' v-model='username' required>
    </div>
    <div>
      <input type='password' class='inputLogin' placeholder='password' minlength="6" v-model='password' required>
    </div>
    <div>
      <input type='password' class='inputLogin' placeholder='confirm password' minlength="6" v-model='cpassword' required>
    </div>
    <button class='buttonLogin' v-on:keyup.enter="singup" @click='singup'>Sing up</button>
    <p @click='unshow' class='linkRegister'>Log in?</p>
  </div>
</div>
</template>

<script class='scoped'>
export default {
  name: 'Auth',
  data(){
    return {
      username: '',
      password: '',
      cpassword: '',
      email: '',
      showReg: false,
      error: null,
      array: [],
      mes: '',
      errorLogin: '',
      errorPass: ''
    }
  },
  computed: {
    er(){
      this.array = []
      this.mes = ''
      for (var i in this.error){
        this.mes = i + ': ' + this.error[i]
        this.array.push(this.mes)
      }
      return this.array
    },
  },
  methods: {
    login(){
      this.$http.post('//kiwido.site/auth/token/create/', {
        username: this.username, password: this.password
      }).then((response) => {
        localStorage.token = response.data.auth_token
        document.location.reload(true)
      }).catch(error => {
        this.errorLogin = 'check entered login and password'
      })
    },
    show(){
      this.showReg = true
    },
    unshow(){
      this.showReg = false
    },
    singup(){
      if (this.password === this.cpassword){
        this.axios.post('//kiwido.site/auth/users/create/', {
          username: this.username, password: this.cpassword, email: this.email
        }).then((response)=>{
          this.$http.post('//kiwido.site/auth/token/create/', {
            username: this.username, password: this.cpassword
          }).then((response) => {
            localStorage.token = response.data.auth_token
            document.location.reload(true)
          })
        }).catch(error => {
          this.error = error.response.data
        });
      }
      else {
        this.errorPass = 'password: Please confirm password'
      }
    }
  }
}
</script>
<style scoped>
*, ::after, ::before {
  box-sizing: content-box!important;
}
.auth{
  text-align: center;
}
.boxLogin h1{
  text-transform: uppercase;
}
.boxLogin{
  width: 300px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  box-shadow: 0 0 10px rgba(0,0,0,0.3);
  padding: 50px;
  z-index: 9999;
}
.inputLogin {
  outline: none;
  border: 3px solid lightblue;
  display: block;
  background: none;
  border-radius: 50px;
  margin:20px auto;
  width:200px;
  padding: 15px;
  text-align: center;
  font-size: 20px;
  transition: 0.3s;
}
.error{
  color: white;
  background-color: #d12d3c;
  padding: 10px;
}
.inputLogin:focus{
  width: 250px;
  border: 3px solid lightgreen;
}
.buttonLogin {
  border-radius: 20px;
  background-color: white;
  border: 3px solid lightgreen;
  height: 50px;
  width: 150px;
  font-size: 20px;
  color: #7e838a;
  cursor: pointer;
  transition: 0.3s;
}
.buttonLogin:hover {
  background-color: lightgreen;
  color: white;
}
.linkRegister{
  padding: 10px;
  cursor: pointer;
  color: #2bc1f2;
  text-decoration: underline;
}
</style>
