<template>
  <div class='container marMiniNotes MiniNotes'>
    <h1 class='logo'>Mini Notes</h1>
    <input @keyup.enter='addNote' type="text" class="todo-input" v-model='newNote' placeholder="What needs to be done...">
    <draggable
      class="dragArea list-group"
      :list="Fnotes"
      ghost-class='ghost'
      :clone="clone"
      :group="{ name: 'people', pull: pullFunction }"
      @start="start"
      @end='onEnd'
    >

      <div class='todo-item container' v-for='note in Fnotes'>
        <div class='todo-item-left'>
          <input class='checkbox' type="checkbox" @change='doneNote(note)' v-model='note.done' >
          <div @dblclick='editNote(note)' :class='{completed : note.done}' class='item' v-if='!note.editing'>{{ucFirst(note.note)}}</div>
          <input class='item-edit' @blur='doneEdit(note)' @keyup.enter="doneEdit(note)" v-model='note.note' v-if='note.editing' type='text' v-focus>
          <img v-if='!note.editing' @click='editNote(note)' class='imgEdit' src="https://img.icons8.com/pastel-glyph/64/000000/edit.png" height='20px'>
        </div>
          <div class='remove-item' @click='deleteNote(note)'>
            &times;
          </div>
        </div>
    </draggable>
      <div class='menu'>
        <div class='check'>
          <button :class="{ active: filter == 'all' }" @click="filter = 'all'">All</button>
          <button :class="{ active: filter == 'active' }" @click="filter = 'active'">Active</button>
          <button :class="{ active: filter == 'completed' }" @click="filter = 'completed'">Completed</button>
        </div>
        <div class='check'>
          {{iLeft}} Items left
        </div>
      </div>
    </div>
</template>

<script>
import draggable from 'vuedraggable'
export default{
  data(){
    return {
      notes: null,
      link: '',
      newNote: '',
      addlink: '',
      list: '',
      filter: 'all',
    }
  },
  components: {
    draggable,
  },
  created(){
    this.link = '//kiwido.site/api/mnote/get/'
    this.link = this.link + localStorage.token +'/'
    this.axios.get(
      this.link
    ).then((response) => {
      this.notes = response.data
      this.list = this.notes.filter(note => !note.done)
    })
  },
  directives: {
    focus: {
      inserted: function (el) {
        el.focus()
      }
    }
  },
  computed: {
    iLeft(){
      return this.notes.filter(note => !note.done).length
    },
    Fnotes() {
      if (this.filter == 'all') {
        return this.notes
      } else if (this.filter == 'active') {
        return this.notes.filter(note => !note.done)
      } else if (this.filter == 'completed') {
        return this.notes.filter(note => note.done)
      }
      return this.notes
    },
  },
  methods: {
    onEnd: function(evt){
      for (var i in this.Fnotes){
        this.link = '//kiwido.site/api/mnote/edit/'
        if (this.Fnotes[i].done) {
          this.link = this.link + localStorage.token +'/'+this.Fnotes[i].id + '/' + this.Fnotes[i].note + '/' + 1 + '/' + i + '/'
          this.axios.get(
            this.link
          ).then((response) => {
            this.add = false
          })
        }
        else{
          this.link = this.link + localStorage.token +'/'+this.Fnotes[i].id + '/' + this.Fnotes[i].note + '/' + 0 + '/' + i + '/'
          this.axios.get(
            this.link
          ).then((response) => {
            this.add = false
          })
        }
      }
      this.link = '//kiwido.site/api/mnote/get/'
      this.link = this.link + localStorage.token +'/'
      this.axios.get(
        this.link
      ).then((response) => {
        this.notes = response.data
        this.list = this.notes.filter(note => !note.done)
      })
    },
    ucFirst(str) {
      if (!str) return str;
      return str[0].toUpperCase() + str.slice(1);
    },
    addNote(){
      this.addlink = '//kiwido.site/api/mnote/post/'
      this.addlink = this.addlink+localStorage.token+'/'+this.newNote+'/'
      this.axios.get(
        this.addlink
      ).then((response) => {
        document.location.reload(true)
      })
    },
    clone({ name }) {
      return { name, id: idGlobal++ };
    },
    pullFunction() {
      return this.controlOnStart ? "clone" : true;
    },
    start({ originalEvent }) {
      this.controlOnStart = originalEvent.ctrlKey;

    },
    editNote(note){
      note.editing = true
    },
    doneEdit(note){
      if (note.done){
        this.addlink = '//kiwido.site/api/mnote/edit/'
        this.addlink = this.addlink+localStorage.token+'/'+note.id+'/' + note.note + '/' + 1 +'/' + note.number + '/'
        this.axios.get(
          this.addlink
        ).then((response) => {
          note.editing = false
        })
      }
      else {
        this.addlink = '//kiwido.site/api/mnote/edit/'
        this.addlink = this.addlink+localStorage.token+'/'+note.id+'/' + note.note + '/' + 0 +'/' + note.number + '/'
        this.axios.get(
          this.addlink
        ).then((response) => {
          note.editing = false
        })
      }
    },
    deleteNote(note){
        this.addlink = '//kiwido.site/api/mnote/delete/'
        this.addlink = this.addlink+localStorage.token+'/'+note.id+'/'
        this.axios.get(
          this.addlink
        ).then((response) => {
          note.editing = false
          document.location.reload(true)
        })
    },
    doneNote(note){
      if (note.done){
        this.addlink = '//kiwido.site/api/mnote/edit/'
        this.addlink = this.addlink+localStorage.token+'/'+note.id+'/' + note.note + '/' + 1 +'/' + note.number + '/'
        this.axios.get(
          this.addlink
        ).then((response) => {
          note.editing = false
        })
      }
      else{
        this.addlink = '//kiwido.site/api/mnote/edit/'
        this.addlink = this.addlink+localStorage.token+'/'+note.id+'/' + note.note + '/' + 0 +'/' + note.number + '/'
        this.axios.get(
          this.addlink
        ).then((response) => {

          note.editing = false
        })
      }
    },
    checkAll(){
        this.list.forEach((note) => (note.done = event.target.checked, console.log(note.id)), console.log('fs'))
    },
  }
}
</script>
<style>

.MiniNotes .flip-list-move{
  transition: transform 0.5s;
}
.MiniNotes .ghost{
  box-shadow: 0 0 10px rgba(0,0,0,0.2);
  opacity: 5;
}
.checkbox{

}
.MiniNotes .check button{
  border:0px solid white;
  border-radius: 50px;
  font-size: 12px;
  background-color: white;
}
.MiniNotes .active{
  background-color: lightgreen!important;
}
.MiniNotes .imgEdit{
  padding-right: 20px;
}
.MiniNotes .check{
  padding:10px;
}
.MiniNotes .menu{
  border-top: 1px solid;
  margin: 14px;
  justify-content: space-between;
  display: -webkit-box;
}
.MiniNotes .item-edit {
  font-size: 24px;
  color: #2c3e50;
  margin-left: 12px;
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  &:focus {
    outline: none;
  }
}
.MiniNotes .completed {
   text-decoration: line-through;
   color: grey!important;
 }
.MiniNotes .logo{
  margin-bottom: 20px;
}
.MiniNotes .todo-item-left {
  display: flex;
  align-items: center;
}
.remove-item{
  cursor: pointer;
}
.MiniNotes .item {
   padding: 10px;
   font-size: 24px;
   font-family: 'Avenir', Helvetica, Arial, sans-serif;
   color: #404346;
   border: 1px solid white;
   margin-left: 12px;
 }
.MiniNotes .todo-input {
  width: 100% ;
  padding: 10px 18px;
  font-size: 18px;
  margin-bottom: 16px;
  border:1px solid #c9c9c9;
  &:focus {
    outline: 0;
  }
}
 .todo-item{
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.marMiniNotes  {
  padding:10%;
  margin-top: 0px;
}
body{
  background: white!important;
}
</style>
